<template>
	<layout>
		<div class="clearfix center p2">
			<p class="h1">Google Map Position Transformer</p>
			<div class="col-12 max-width-2 mx-auto">
				<input type="text" class="input center not-rounded" v-model="position" placeholder="è¿™é‡Œç²˜è´´GoogleMapå¤åˆ¶ç»“æžœï¼Œå¦‚ï¼š39.803906, 116.512255">
				<a class="btn btn-primary col-12 max-width-2 not-rounded" @click="trans">è½¬ æ¢</a><br>
			</div>
			<div class="col-12 max-width-2 mx-auto" v-if="transed">
				<p class="bold">è½¬æ¢ç»“æžœï¼š{{ latlng }}</p>
				<p class="mb0">åŠ ä¸Š origin@ æˆ– produce@ æ‰æ˜¯ åŽŸäº§åœ° æˆ– ç”Ÿäº§åœ° åæ ‡</p>
				<p class="mt0">é€‰æ‹©ä¸‹é¢å…¶ä¸­çš„ä¸€ä¸ªå¤åˆ¶ï¼Œåˆ° Shopify åŽ»ç²˜è´´ä½¿ç”¨</p>
				<hr>
				<p class="bold">åŽŸäº§åœ°åæ ‡ï¼š</p>
				<input type="text" class="input center not-rounded" v-model="origin">
				<a class="btn btn-primary col-12 max-width-2 not-rounded" @click="copys('åŽŸäº§åœ°', origin)">å¤åˆ¶ åŽŸäº§åœ° åæ ‡</a>
				<hr>
				<p class="bold">ç”Ÿäº§åœ°åæ ‡ï¼š</p>
				<input type="text" class="input center not-rounded" v-model="produce">
				<a class="btn btn-primary col-12 max-width-2 not-rounded" @click="copys('ç”Ÿäº§åœ°', produce)">å¤åˆ¶ ç”Ÿäº§åœ° åæ ‡</a>
				<br><br>
			</div>
		</div>
	</layout>
</template>

<script>
import Layout from '@/components/Layout'
export default {
	name: 'TransLocation',
	data () {
		return {
			position: '',
			latlng: '',
			origin: '',
			produce: '',
			transed: false,
			copied: false
		}
	},
	methods: {
		trans () {
			let self = this
			let locations = self.position.split(', ')
			if (typeof locations[0] !== 'boolean' && !isNaN(locations[0]) && locations[0] !== ''
				&& typeof locations[1] !== 'boolean' && !isNaN(locations[1]) && locations[1] !== '') {
				let lat = locations[0] < 0 ? 'S' + Math.abs(locations[0]) : 'N' + locations[0]
				let lng = locations[1] < 0 ? 'W' + Math.abs(locations[1]) : 'E' + locations[1]
				self.latlng = lat + ';' + lng
				self.origin = 'origin@' + self.latlng
				self.produce = 'produce@' + self.latlng
				self.transed = true
			} else {
				swal('æ‚¨è¾“å…¥çš„åæ ‡ä¿¡æ¯æœ‰é”™', 'è¯·æ£€æŸ¥æ‚¨çš„è¾“å…¥æ˜¯å¦æ­£ç¡®', 'error')
			}
		},
		copys (alt, loc) {
			if (this.$clipboard(loc)) {
				swal(alt + 'åæ ‡å·²å¤åˆ¶æˆåŠŸ', loc, 'success')
			} else {
				swal(alt + 'åæ ‡å¤åˆ¶å¤±è´¥', 'è¯·åˆ·æ–°é‡è¯•', 'error')
			}
		}
	},
	components: {
		Layout: Layout
	}
}
</script>

<style scoped>
hr {
	border: 3px solid #eee;
}
</style>



